.PickFive {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-top: calc(40px + 2vw);
  margin-bottom: 100px;
  height: calc(300px + 10vw);
  font-size: calc(8px + 0.4vw);
}

.PickFive > div {
  max-width: 480px;
  padding: 20px;
  margin: 20px 20px 20px 20px;
}

.slick-slider {
  width: 80%;
  height: 100%;
  border-radius: 8px;
  border: 4px solid rgb(255, 255, 255, 0.8);
  background-color: rgb(255, 255, 255, 0.15);
}

.slick-list {
  height: 100%;
}

.slick-slide {
  width: 240px;
  padding: 40px;
}

.PickFive h3 {
  font-size: calc(12px + 0.4vw);
  border: calc(1px + 0.1vw) solid;
  border-radius: 8px;
  text-align: center;
  padding: 8px;
  margin-left: 20px;
  margin-right: 20px;
}

.PickFive h3:hover {
  background-color: rgb(255, 255, 255, 0.3);
}

.PickFive h3:active {
  color: #fff200;
  border-color: #fff200;
  background-color: rgb(0, 0, 0, 0.1);
}

.PickFive div:focus {
  outline: none;
}

.PickFiveDescription {
  margin: auto;
  margin-top: calc(10px + 3vw);
  width: 60%;
  max-width: 400px;
  border-radius: 8px;
  border: 2px solid;
  background-color: rgb(0, 0, 0, 0.8);
  padding: 10px;
  font-size: calc(10px + 0.2vw);
  font-weight: 500;
}

#dim {
  display: flex;
  flex-direction: column;
}

@media only screen and (max-width: 800px) {

  .PickFive {
    flex-direction: column;
    height: 600px;
    align-items: center;
  }

  .PickFive > div {
    max-height: 120px;
  }

}